
@mixin flex-row {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin flex-column {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

li {
  list-style: none;
}

a {
  color: black;
  text-decoration: none;
}

.App {
  text-align: center;
  .header {
    @include flex-row();
    justify-content: space-around;
    height: 17.5vh;
    background-color: black;
    * {
      color: white;
    }
    .buscador {
      height: 4vh;
      width: 15vw;
    }
  }
  #navbar {
    height: 7.5vh;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
    #logo {
      width: 30%;
      display: flex;
      justify-content: center;
    }
    #menu-desktop {
      width: 100vw;
      display: flex;
      justify-content: center;
      align-items: center;
      
      > * {
        margin: 0 2.5%;
      }
      > *:hover {
        text-decoration: underline;
      }
      .nosotros {
        position: relative;
        cursor: pointer;
        .desplegable {
          display: none;
          position: absolute;
          z-index: 2;
          padding-top: 15px;
          left: -8px;
          > * {
            text-align: left;
            width: 120px;
            background: #c2e9fb;
            padding: 15px 25px;
          }
          
          :nth-child(3) {
            padding-bottom: 20px;
          }
        }
      }
      .nosotros:hover {
        
        .desplegable {
          display: flex;
          flex-direction: column;
        }
        .desplegable>*:hover {
          background-color: #a1c4fd;
        }
      }
    }
    
    #menu-mobile {
      display: none;
    }
    .none, .mobile-links {
      display: none;
    }
  }

  // footer 
  .footer {
    @include flex-row();
    gap: 10%;
    height: 25vh;
    background-image: linear-gradient(to top, #d5d4d0 0%, #d5d4d0 1%, #eeeeec 31%, #efeeec 75%, #e9e9e7 100%);
    // position: absolute;
    // width: 100%;
  }
}

.inicio {
  @include flex-column();
  // height: 300px;
  width: 100%;
  // background-color: black;
  
}

@media (max-width: 760px) {
  .App {
    .header {
      @include flex-column();
      height: 20vh;
      .buscador {
        display: none;
      }
    }
    #navbar {
      gap: 70%;
      width: 100%;
      height: 10vh;
      #logo {
        cursor: pointer;
      }
      #menu-desktop {
        display: none;        
      }
      #menu-mobile {
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 100vw;
        position: relative;
        .svg-menu {
          width: 30vw;
          display: flex;
          justify-content: flex-start;
          height: 30px;
          width: 30px;
          color: rgba(56, 49, 119, 0.74);
          @include flex-row();
          border: 2px solid rgba(56, 49, 119, 0.301);
          border-radius: 5px;
          padding: 1px;
          box-shadow: 0 0 2px 0 gray;
        }
        .svg-menu:hover, .svg-menu:active {
          color: rgba(56, 49, 119, 0.911);
          border: 2px solid rgba(56, 49, 119, 0.829);
          box-shadow: 0 0 8px 0 gray;
          transform: scale(1.05);
          transition: 0.5s linear;
        }
        .buscador {
          width: 50vw;
          height: 3.5vh;
        }
        
      }
      .none {
        display: none;
      }
      .mobile-links {
        z-index: 3;
        @include flex-column();
        justify-content: flex-start;
        position: absolute;
        top: 30vh;
        width: 100vw;
        // height: 90vh;
        background: black;
        border: 3px solid gray;
        letter-spacing: 2px;
        .link {
          width: 100vw;
          color: white;
          padding: 5% 0;
          background-color: black;
          box-shadow: 0 0 3px 0.5px gray;
        }
        .f1 {
          font-size: 1.1em;
        }
        .underline {
          text-decoration: underline gray;
        }
        // >.link:hover, >.link:active {
         
        // }
        .nosotros-mobile {
          .desplegable-mobile {
            @include flex-column();
            height: 25vw;
            margin: 17% 0 12.5% 0;
            .link-2 {
              font-size: 1em;
              color: white;
              padding: 5% 0;
              text-decoration: none;
              width: 100vw;
              background-color: rgb(85, 84, 84);
              box-shadow: 0 0 1px 0 black;
            }
            // > .link-2:hover, > .link-2:active {
            //   background-color: gray;
            // }
          }
        }
      }
    }
    .footer {
      height: 70vh;
      @include flex-column();
    gap: 15%;
    }
      
    
  }
}

button {
  background: none;
  border: none;
  cursor: pointer;
}

button:focus {
  outline: none;
}